import {axiosInstance, LOGOUT_SUCCESS, REFRESH_FAILURE} from '../../../Login/modules/login';
import Config from 'Config';

export const GET_USER_SUCCESS = 'GET_USER_SUCCESS';
export const GET_USER_FAILURE = 'GET_USER_FAILURE';

export function getUser(user,loginCallback){
  const url = `${Config.API_URL}users/me`;
  return (dispatch) => {
    axiosInstance.get(url,{headers: {'Authorization': `Bearer ${user.AccessToken}`}})
    .then((res)=> {
      return dispatch({
        type:GET_USER_SUCCESS,
        profile:res.data
      })
    })
    .catch((err) => {
      if(err == REFRESH_FAILURE){
        return dispatch({
          type: LOGOUT_SUCCESS
        })
        //dispatch logout redurect to login
      }
      else{
        //dispatch get user fail
        return dispatch({
          type:GET_USER_FAILURE,
          err:err.response.error
        })
      }
    });
  }
}



