import './UserProfile.scss';

import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Link} from 'react-router';
import Sidebar from '../../Sidebar/components/Sidebar.js';
import Header from '../../../Header/components/Header.js';
import {getUser} from '../modules/profile';
import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class UserProfileContainer extends React.Component {
    constructor(props) {
        super(props);
    }

    componentWillReceiveProps(nextProps){
      if(!nextProps.auth.loggedIn){
        this.goToIndex();
      }
    }

    onChange = () => {}

    goToIndex = () => {
        this.props.router.push('/');
    }
    goToLogin = () => {
      this.props.router.push('/login');
    }

    goToUserBuyDroplet = () => {
      this.props.router.push('/user-buyDroplet');
    }

    componentDidMount(){
      if(this.props.auth.loggedIn){
        const {user} = this.props.auth;
        this.props.actions.getUser(user,() => {this.goToLogin});
      }
    }

    render() {
      if(!this.props.auth.loggedIn){
        this.goToIndex();
      }
        return (
            <div className="user-profile">
                <Header goHome={this.goToIndex} isLoggedIn={true} />
                <Sidebar />
                <div className="profile-section">
                  <div id="title1" className="title-text">User profile</div>
                  <label className="label-name label-input" htmlFor="name">Full Name (as per NRIC)</label>
                  <input type="text" className="form-control" id="name"/>
                  <label className="label-input label-nric" htmlFor="nric">NRIC Number</label>
                  <input type="text" className="form-control" id="nric"/>
                  <label className="label-input label-email" htmlFor="email">Email</label>
                  <input type="text" className="form-control" id="email"/>
                  <p className="note">For claims pay out, this email  must match the email you use for your GRAB rides.</p>
                </div>
                <div className="paynow-section">
                  <div id="title2" className="title-text">PayNow Details</div>
                  <label className="label-mobile label-input" htmlFor="mobileNumber">Mobile Number</label>
                  <input type="text" className="form-control" id="mobileNumber" value="9748 9472" onChange={this.onChange}/>
                  <label className="label-input label-check"><i className="fas fa-check"></i> ELSON NG KAI REN, +65 9748 9472</label>
                  <p className="note">The same mobile number you use for your PayNow account.</p>
                </div>
                <div className="mobile-section">
                  <div id="title3" className="title-text">Notification Settings</div>
                  <label className="label-mobile label-input" htmlFor="mobileNumber">
                      Receive messages from us regarding your claims and promotions.
                  </label>
                  <label className="label-email form-check-label green-checkbox">
                    <input id="emailChk" type="checkbox" className="form-check-input"/>
                    <i className="helper email-helper"></i>
                    <strong className="lbl-text">Email</strong>
                  </label>
                  <label className="label-sms form-check-label green-checkbox">
                    <input id="smsChk" type="checkbox" className="form-check-input" value={true}/>
                    <i className="helper sms-helper"></i>
                    <strong className="lbl-text">SMS</strong>
                  </label>
                </div>
                <div className="password-section">
                  <div id="title4" className="title-text">Reset Password?</div>
                  <label className="label-current label-input" htmlFor="currentPassword">Current Password</label>
                  <input type="password" className="form-control" id="currentPassword" onChange={this.onChange}/>
                  <label className="label-new label-input" htmlFor="newPass">New Password</label>
                  <input type="password" className="form-control" id="newPass" onChange={this.onChange}/>
                  <label className="label-retype label-input" htmlFor="confirmNewPass">Retype New Password</label>
                  <input type="password" className="form-control" id="confirmNewPass" onChange={this.onChange}/>
                </div>
                <button className="btn btn-green btn-save">Update and Save</button>
            </div>
        );
    }
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({getUser}, dispatch),
    };
}

const mapStateToProps = (state) => {
  console.log(state);
  return {auth: state.auth};
};


export default connect(mapStateToProps,mapDispatchToProps)(UserProfileContainer)



