import React from 'react';
import ReactDOM from 'react-dom';
import './Header.scss';
import { Link } from 'react-router';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { userLogout } from '../../Login/modules/login';
import background1 from '../assets/bg-1.png';
import logo from '../assets/droplet-logo.png';
import logoDark from '../assets/droplet-logo-dark.png';
import logOutImg from '../assets/logout-icon.png';

class Header extends React.Component{
  constructor(props){
    super(props);
  }

  homeHeader(){
    return(
      <div className="header-section sticky-header" style={{backgroundImage: `url(${background1})`}}>
        <div className="header-left">
            <img src={logo} className="droplet-logo" onClick={this.props.isHome? '' : this.props.goHome }/>
        </div>
        {this.homeHeaderRightSection()}
      </div>
    );
  }

  otherHeader(){
    return(
      <div className="header-section" >
        <div className="header-left">
            <img src={logoDark} className="droplet-logo" onClick={this.props.goHome }/>
        </div>
        {this.headerRightSection()}
      </div>
    );
  }

  logout = () =>{
    console.log('logging out');
    this.props.actions.userLogout(null, this.props.goHome);
  }

  homeHeaderRightSection(){
    const {user} = this.props.auth;
    if(this.props.auth.loggedIn){
      return(
        <div className="header-right dropdown">
          <label className="header-text clickable dropdown-toggle">Welcome {(user && user.name) ? user.name : ''}</label>
          <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <Link className="dropdown-item" to="/droplet-history">Droplet History</Link>
            <Link className="dropdown-item" to="/user-buyDroplet">My Profile</Link>
            <label className="dropdown-item" onClick={this.logout}>Logout</label>
          </div>
          <button className="btn btn-buy-now" onClick={this.props.buyNow}>BUY NOW</button>

        </div>
    );
    }
    else{
      return(
        <div className="header-right">
          <label className="header-text pointer it-works" onClick={this.props.instructions}>How it Works</label>
          <label className="header-text pointer login-btn" onClick={this.props.login}>Login</label>
          <button className="btn btn-buy-now" onClick={this.props.buyNow}>BUY NOW</button>
        </div>
    );
    }
  }

  headerRightSection(){
    const {user} = this.props.auth;
    if(this.props.auth.loggedIn){
      return(
        <div className="header-right dropdown">
          <label className="header-text-white clickable dropdown-toggle">Welcome {(user && user.name) ? user.name : ''}</label>
          <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <Link className="dropdown-item" to="/droplet-history">Droplet History</Link>
            <Link className="dropdown-item" to="/user-buyDroplet">My Profile</Link>
            <label className="dropdown-item" onClick={this.logout}>Logout</label>
          </div>
          <button className="btn btn-buy-now" onClick={this.props.buyNow}>BUY NOW</button>

        </div>
      );
      }
    else{
      const loginClass = this.props.isHome ? 'header-text login-btn pointer' : 'header-text-dark login-btn pointer';
      return(
        <div className="header-right">
          <label className={loginClass} onClick={this.props.login}>Login</label>
        </div>
      );
    }
  }

  render(){
    return(
      <div className="header-parent">
        {this.props.isHome ? this.homeHeader(): this.otherHeader()}
        </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {auth: state.auth};
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({userLogout}, dispatch),
    };
}



export default connect(mapStateToProps,mapDispatchToProps)(Header);



